// vuex持久化处理 这里面封装着将vuex的值放进本地存储的函数体们

// $ 这是多个组件都要用的数据:要做持久化处理的思路,先通过封装函数存进我们的本地localStorage,然后把被封装的函数拿给vuex即可(vuex可让数据在组件们中流通)(记得import一下)
// $ 单个组件要用的数据:直接存进本地localStorage,通过封装函数存进我们的本地localStorage,然后通过封装函数调用即可(记得import一下)
// 约定一个通用的键名 (用户权证在本地存储的名字)
const INFO_KEY = 'user_shopping_info'
// 搜索历史记录的键名(搜索记录持久化)
const HISTORY_KEY = 'user_history_list'

// 获取个人信息
export const getInfo = () => {
  const defaultObj = { token: '', userId: '' }
  const result = localStorage.getItem(INFO_KEY)
  return result ? JSON.parse(result) : defaultObj
}
// 设置个人信息
export const setInfo = (obj) => {
  localStorage.setItem(INFO_KEY, JSON.stringify(obj))
}
// 移除个人信息
export const removeInfo = () => {
  localStorage.removeItem(INFO_KEY)
}
// 获取历史记录
export const getHistoryList = () => {
  const defaultArr = []
  const result = localStorage.getItem(HISTORY_KEY)
  return result ? JSON.parse(result) : defaultArr
}
// 设置历史记录
export const setHistoryList = (arr) => {
  localStorage.setItem(HISTORY_KEY, JSON.stringify(arr))
}
