<template>
  <div class="cart">
    <van-nav-bar title="购物车" fixed />
    <div v-if="isLogin && cartList.length>0">
    <!-- 购物车开头 -->
    <div class="cart-title">
      <span class="all">共<i>{{cartTotal}}</i>件商品</span>
      <span class="edit" @click="isEdit = !isEdit">
        <van-icon name="edit" />
        编辑
      </span>
    </div>

    <!-- 购物车列表 -->
    <div class="cart-list">
      <div class="cart-item" v-for="item in cartList" :key="item.goods_id">
        <van-checkbox @click="toggleCheck(item.goods_id)" :value="item.isChecked"></van-checkbox>
        <div class="show">
          <img :src="item.goods.goods_image" alt="">
        </div>
        <div class="info">
          <span class="tit text-ellipsis-2">{{ item.goods.goods_name }}</span>
          <span class="bottom">
            <div class="price">¥ <span>{{ item.goods.goods_price_min }}</span></div>
            <!-- // 更改购物车单个商品+ - 多少件 -->
            <!-- 既希望保留CountBox传过来的值(形参),又需要通过调用changCount传item的参,那该怎么办
                => 箭头函数包装一层 (注意一定要把value单独拿出来,不然这个形参就被item那群参数顶掉了)
                => 这样就能将item的值保留到参数中了,而method中也可以用item了！
                .很重要,这是我了解到唯一能把item带出去的方法
            -->
            <CountBox @input="(value)=>changeCount(value,item.goods_id,item.goods_sku_id)"
              :value="item.goods_num"></CountBox>

            <!-- * <CountBox @input="changeCount" :value="item.goods_num"></CountBox> -->
            <!-- * 这个changeCount方法函数会自带CountBox这个子组件传过来的值,
              *而那个值在changeCount这个函数的方法的形参中(也就是e)
              *而你也可以通过 $event.target.value 获取-->
          </span>
        </div>
      </div>
    </div>
    <!-- 购物车底部 -->
    <div class="footer-fixed">
      <div  class="all-check">
        <van-checkbox  icon-size="18" v-model="isAll"></van-checkbox>
        全选
      </div>
        <!-- 购物车结算 -->
      <div class="all-total">
        <div class="price">
          <span>合计：</span>
          <span>¥ <i class="totalPrice">{{selTotalPrice}}</i></span>
        </div>
        <div @click="goPay" v-if="!isEdit" class="goPay" :class="{disabled:selTotalCount===0}">结算({{selTotalCount}})</div>
        <div v-else @click="handleDel" class="delete" :class="{disabled:selTotalCount===0}">删除</div>
      </div>
    </div>
  </div>
  <div class="empty-cart" v-else>
  <img src="@/assets/empty.png" alt="">
  <div class="tips">
    您的购物车是空的, 快去逛逛吧
  </div>
  <div class="btn" @click="$router.push('/')">去逛逛</div>
</div>
</div>
</template>

<script>

import CountBox from '@/components/CountBox.vue'
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'CartPage',
  data () {
    return {
      isEdit: false
    }
  },
  components: {
    CountBox
  },
  created () {
    // 必须是登录过得用户才能获得购物车列表
    if (this.$store.getters.token) {
      // 这个是调用cart仓库里的异步方法getCartAction,目的是在页面渲染前 往vuex中存购物车列表
      this.$store.dispatch('cart/getCartAction')
      // console.log(this.cartList)
    }
  },
  computed: { // !!!简单写法必先return起手
    // 因为上面的 created ()已经存了购物车列表进vuex了,所以我们在这拿到购物车列表
    ...mapState('cart', ['cartList']),
    // 这边的数都能当自家人用
    ...mapGetters('cart', ['cartTotal', 'selCartList', 'selTotalCount', 'selTotalPrice']),
    isAll: { // isAll这是写在大的复选框中的属性(因为是复选框,所以只有true或false)
      get () { // 小的复选框 决定 大的复选框打不打钩, 所以这是被动的,被动的要写在get上,并且没有经过vuex
        return this.cartList.every(item => item.isChecked === true)
      },
      set (value) { // 大的复选框 决定 小的复选框打不打钩,所以这是主动的,要写在set(value)中,并且这是经过vuex的
        // 经过vuex然后修改数据,这时我们要干嘛,要在vuex分仓库cart里加mutations方法提供修改
        this.$store.commit('cart/isAllcheck', value)
      }
    },
    // 为了不频繁判断用户有没有登录,所以将isLogin写在这
    isLogin () {
      return this.$store.getters.token
    }
  },
  methods: {
    // 修改小复选框的isChecked
    toggleCheck (goodsId) {
      this.$store.commit('cart/toggleCheck', goodsId)
    },
    // 更改购物车单个商品+ - 多少件
    // $这边因为既要调用axios 又要将得到的axios传回给vuex,所以一步到位直接用actions
    changeCount (goodsNum, goodsId, goodsSkuId) {
      // console.log(goodsNum, goodsId, goodsSkuId)
      // const res = await changeCount(goodsId, goodsNum, goodsSkuId)
      // $这边因为既要调用axios 又要将得到的axios传回给vuex,所以一步到位直接用actions
      this.$store.dispatch('cart/changeCountAction',
        {
          goodsId,
          goodsNum,
          goodsSkuId
        }
      )
    },
    // $这边也是一样的 既要调用axios 又要将得到的axios传回给vuex,所以一步到位直接用actions
    async handleDel () {
      // 如果结算那数为0那就return
      if (this.selTotalCount === 0) return
      // 这里不传值了,因为分仓库cart里面有被选中的项的值,让它传
      await this.$store.dispatch('cart/delAction') // 这里要await,把它变成同步函数,等这个执行完了,在去把删除切换成结算
      this.$toast('删除成功')
      this.isEdit = false
    },
    // 点击结算按钮并做传参
    goPay () {
      // 没有商品点击结算不做反应
      if (!this.selCartList.length > 0) return
      // 有商品点击结算 , 跳转
      this.$router.push({
        path: '/pay',
        query: {
          mode: 'cart',
          cartIds: this.selCartList.map(item => item.id).join(',')
        }
      })
    }
  },
  watch: { // watch要单独拎出来写,别写在别人的{}里面
    isEdit (value) { // value就是变化后的值
      if (value) { // 只有true能通过
        // 如果isEdit是true,则把小复选框全 不打钩
        this.$store.commit('cart/isAllcheck', false)
      } else {
        this.$store.commit('cart/isAllcheck', true)
      }
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.cart {
  padding-top: 46px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }

  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;

    .show img {
      width: 100px;
      height: 100px;
    }
    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .bottom {
        display: flex;
        justify-content: space-between;
        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;
          span {
            font-size: 16px;
          }
        }
        .count-box {
          display: flex;
          width: 110px;
          .add,
          .minus {
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
          }
          .inp {
            width: 40px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            text-align: center;
            margin: 0 5px;
          }
        }
      }
    }
  }
}

.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;

  .all-check {
    display: flex;
    align-items: center;
    .van-checkbox {
      margin-right: 5px;
    }
  }

  .all-total {
    display: flex;
    line-height: 36px;
    .price {
      font-size: 14px;
      margin-right: 10px;
      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }

    .goPay, .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;
      &.disabled {
        background-color: #ff9779;
      }
    }
  }

}
.empty-cart {
  padding: 80px 30px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}
</style>
