import Vue from 'vue'
import Vuex from 'vuex'
import user from '@/store/modules/user'
import cart from '@/store/modules/cart'
Vue.use(Vuex)
// $ 什么时候要用vuex呢,需要多次操作增删的一些数据。
// 如果是获取接口数据单纯用来渲染的话,建议流程如下:
/*
  =>api 封装函数 => getList()
  =>组件 methods中 封装api的封装函数 =>
  async abcdefg(){
    const res = await getList()
    this.xxx=res.yyy //^获取的数据存到data中了,但现在还没存！在created中运行这个函数,就会在created阶段存了！
  }
  =>组件 created中 this.abcdefg() (在页面渲染前使用一下在created调用一下methods封装的api函数 )
*/
export default new Vuex.Store({
  state: {
  },
  getters: {
    token (state) {
      // 主仓库要去取到分仓库内的数据 必须要state在前.分仓库在后.
      // 就是state.分仓库名.xx.xx
      return state.user.userInfo.token
    }
  },
  mutations: {

  },
  actions: {
  },
  modules: {
    user,
    cart
  }
})
