import { changeCount, delSelect, getCartList } from '@/api/cart'
export default {
  namespaced: true,
  state () {
    return {
      cartList: []
      // cartTotal: ''
    }
  },
  mutations: {
    // 给actions 提供一个设置 cartList 的 mutations方法(存放cartList)
    setCartList (state, newList) { // 前者(state)是自己的,后者(newList)是别人传进来的
      state.cartList = newList
    },
    // 提供一个给外部组件能够修改小复选框的isChecked的mutations
    toggleCheck (state, goodsId) {
      /* if (state.cartList.goods_id === goodsId) {
        ? 要遍历对象或者用数组方法找而不是直接if找,在这if最多判断一项
        state.cartList.isChecked = !state.cartList.isChecked
      } */
      // const goods = state.cartList.filter(item => item.goods_id === goodsId)
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      // console.log(goods)
      // 为什么这里能修改到,因为用find的话,是找到item,也就是返回值是个对象
      // 而用filter的话,它的返回值是个数组对象
      // 用filter的修改方式 goods[0].isChecked = !goods[0].isChecked
      // 用find的修改方式
      goods.isChecked = !goods.isChecked
    },
    // 提供一个给外部(大复选框)能修改所有复选框的mutations
    isAllcheck (state, flag) {
      // 给所有小的复选框赋值,赋的值是大的复选框传过来的是,大的复选框什么状态,小的们就是什么状态
      state.cartList.forEach(item => { item.isChecked = flag })// 这里是赋值,不是判断,于是用=,别用===
      // 注意forEach是没有return的数组方法,而且它是纯遍历
    },
    // 给actions 提供一个点+-然后 能改变购物车总数的 mutations方法
    changeCount (state, { goodsId, goodsNum }) { // 因为这里只能有两个形参,所以用对象包着
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.goods_num = goodsNum
    }
  },
  actions: {
    // 内用:调用api把购物车列表 接进 仓库state
    async getCartAction (context) {
      const res = await getCartList()
      //! 要getCartList() 而不是getCartList,因为你要调用它,所以要加()让它执行并返回它获取的值给你
      // console.log(res)
      // ? 后台返回的res.data.list中没有商品的复选框是否选中的属性,所以我们这里手动添加进cartList去,一样达到效果
      // ? 我们会给每一项数据,添加一个isChecked状态,用来标记当前商品是否选中(天才!!手动加数据进返回来的数据)
      res.data.list.forEach(item => { item.isChecked = true })

      // 把res.data.list的值通过mutations封装到这个分仓库的cartList里
      context.commit('setCartList', res.data.list)
      // context.commit('setCartTotal', res.data.cartTotal)
    },
    // 外用:接收外部组件传进来的购物车 商品数量更新值,然后调用api,将更新值接进 仓库state
    async changeCountAction (context, obj) {
      const { goodsNum, goodsId, goodsSkuId } = obj
      // 先在vuex修改这个+ -后的 仓库状态(actions修改仓库要写context.commit(借助mutations))
      context.commit('changeCount', { goodsId, goodsNum })
      // 再上传到后端(为什么这么干,因为直接操作vuex是没有延迟的,vuex的其他数据也会跟着+ - 一起变动){而如果操作一下重新渲染一下就会有延迟}
      await changeCount(goodsId, goodsNum, goodsSkuId)
      /*  // 重新拉取最新的购物车数据(重新渲染)
      | 不能这么干,会有延迟! 点+ - 之后数字先动了, 过一秒 其他数据才动
      | 可能在这点+ -就会触发axios请求,就是当前这个函数(changeCountAction)
      | 然后又通过这个函数调用了另一个异步actions函数getCartAction,可能就会导致双重调用就很慢很卡
      context.dispatch('getCartAction') */
    },
    // 外用:删除选中的购物车单
    // . 有context就能调用state,getters,mutations,总之这个axios接口需要被传的值getters有,就不用外部传进来了
    async delAction (context) {
      const selCartList = context.getters.selCartList
      // .看,这就能拿到了getters的值了,然后用这个值 “整理过后” 发给axios就行
      const id = selCartList.map(item => item.id)
      // map() (抓小鸡数组方法,点到谁谁就要被抓出来放在另一个鸡圈数组)
      // 也就是将 原数组的每一个对象的id都单独拿出来形成一个新的数组,id=[id1,id2,id3]
      await delSelect(id)// 被打了钩的id
      // 重新渲染最新的购物车数据
      // .又学一招,可以在actions里面调用其他actions
      context.dispatch('getCartAction')
    }
  },
  getters: {
    // 求所有商品累加总数(放在购物车左上角的)
    cartTotal (state) {
      return state.cartList.reduce((prev, current) => prev + current.goods_num, 0)
    },
    // 选中的商品项 (被打上钩的)
    selCartList (state) {
      return state.cartList.filter(item => item.isChecked === true)
    },
    // 选中的商品总数(被打上钩的)
    selTotalCount (state, getters) { // 在getters中也可以拿到getters
      // 把上面选中的商品列表拿下来再reduce 就可以少了筛选是否选中的流程了
      return getters.selCartList.reduce((prev, current) => prev + current.goods_num, 0)
    },
    // 选中的商品总价格(被打上钩的)
    selTotalPrice (state, getters) {
      return getters.selCartList.reduce((prev, current) => {
        return prev + current.goods_num * current.goods.goods_price_min
      }, 0).toFixed(2)
    }
  }
}
