import Vue from 'vue'
import VueRouter from 'vue-router'

import store from '@/store'
import User from '@/views/layout/user.vue'
import Cart from '@/views/layout/cart.vue'
import Category from '@/views/layout/category.vue'
import Home from '@/views/layout/home.vue'
import Layout from '@/views/layout'

const Login = () => ('@/views/login/index.vue') // 如果是index.vue,以index命名,只需加载其文件夹即可,vue会自动帮你找到下面的index
const Myorder = () => ('@/views/myorder')
const Pay = () => ('@/views/pay')
const Prodetail = () => ('@/views/prodetail')
const Search = () => ('@/views/search/index.vue')
const SearchList = () => ('@/views/search/list.vue')

// const xx = () => ('@/xxx/xxxx/x/xx') 这种是路由的懒加载,是打包的时候用的,实现打包优化
Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    // 但凡是单个页面,独立展示的,都是一级路由
    // 二级路由会保留它的父路由的内容,这才能称为二级路由
    { path: '/login', component: Login },
    {
      path: '/',
      component: Layout,
      redirect: '/home',
      children: // children是数组里面包对象
      [
        { path: '/home', component: Home },
        { path: '/cart', component: Cart },
        { path: '/category', component: Category },
        { path: '/user', component: User }
      ]
    },
    { path: '/order', component: Myorder },
    { path: '/pay', component: Pay },
    { path: '/prodetail/:id', component: Prodetail },
    // . "为什么要加 /:id?" 动态路由传参法 接收参数的路由要被改造：这样路由参数中就携带id

    { path: '/search', component: Search },
    { path: '/searchlist', component: SearchList }

  ]
})
/*
路由导航守卫 - 全局前置守卫
!1. 所有的路由一旦被匹配到，都会先经过全局前置守卫
!2. 只有全局前置守卫放行，才会真正解析渲染组件，才能看到页面内容
? 访问权限页面时，拦截或放行的关键点？ → 用户是否有登录权证 token
*/
//! 所有的路由在真正被访问到之前(解析渲染对应组件的页面前),都会先经过全局前置守卫
// 全局前置导航守卫
// to:到哪里去:到哪里去的完整路由信息对象 (路径,参数)
// from:从哪里来:从哪里来的完整路由信息对象  (路径,参数)
// next():是否放行
// (1)next() 直接放行 ,放行到to要去的路径
// (2)next(路径) 一旦里面写了路径就是进行拦截了,拦截到next里面配置的路径

// 定义一个数组,专门存放所有用户需要权限才能访问的页面 authUrls:需要授权的路径们
const authUrls = ['/pay', '/order']
router.beforeEach((to, from, next) => {
  // * 注意:to和from对象中有path属性,是不带查询参数的, to.path:到哪里去的路径
  // 但是fullPath携带,想要用查询参数的就用fullPath
  if (!authUrls.includes(to.path)) {
    // 非权限页面,直接放行
    next()
    return
  }
  // 权限页面,判断是否有token
  // const token = store.state.user.userInfo.token(把后者封装进了getters中)
  const token = store.getters.token
  // 有token则放行,否则回到login
  token ? next() : next('/login')
})

export default router
