import axios from 'axios'
import { Toast } from 'vant'
import store from '@/store/index'
// 创建axios实例,将来对创建出来的实例进行自定义配置
// 好处: 独立出一个axios,不会污染原始的axios实例
const instance = axios.create({
  baseURL: 'http://smart-shop.itheima.net/index.php?s=/api',
  timeout: 5000, // 超时时间,来个5秒
  headers: {
    platform: 'H5'
  }
})
// 自定义配置

// 导出配置好的实例 (一般是有实例只用导出实例,其次导出函数)
// 添加请求拦截器
// . 原本是 axios.interceptors.requ...改成 => instance.interceptors.requ... (基于实例化对象修改方法)
instance.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么 (实际是已经发送请求了,但是我们可以在发送的before做一些事情,
  // 比如说中间写个加载...在发送请求之前(因为发送请求也需要网络加载) 到 成功发送请求 中间的那段时间)
  // 开启loading,静止背景点击(节流处理,防止多次无效触发)
  Toast.loading({
    message: '加载中...',
    forbidClick: true, // 禁用用户对背景(屏幕)的点击,达到节流效果
    loadingType: 'spinner', // loading图标
    duration: 0// 展示时长(ms)，值为 0 时，toast 不会消失, 保证一个一直打开的状态,等到成功发送请求了,才关！
  })

  // 只要有token,就在请求中携带,便于请求需要授权的接口
  const token = store.getters.token
  if (token) {
    // 这个config就是上面(第六行) axios.create的配置项
    config.headers['Access-Token'] = token
    config.headers.platform = 'H5'
  }
  return config
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error)
})

// 添加响应拦截器
instance.interceptors.response.use(function (response) {
  // 2xx 范围内的状态码都会触发该函数。
  //* 对响应数据做点什么 (默认axios会多包装一层data,所以在响应拦截器处理一下,给他扒掉一层,所以return response.data)
  const res = response.data
  if (res.status !== 200) {
    // 给错误提示
    Toast(res.message)
    // 抛出一个错误的Promise,不让await往下走
    return Promise.reject(res.message)
  } else {
    // 正确情况,直接走业务的核心逻辑
    Toast.clear() // 清除所有的Toast,我们是为了成功响应后清除Loading加载中的Toast
    // Toast是单例模式,后一个Toast调用了,会将上一个的Toast效果覆盖,同时只能存在一个Toast
  }

  return res
}, function (error) {
  // 超出 2xx 范围的状态码都会触发该函数。
  // 对响应错误做点什么
  return Promise.reject(error)
})
// 导出配置好的实例
export default instance
