import { getInfo, setInfo } from '@/utils/storage'

export default {
  namespaced: true, // 代表它跟其他模块是独立的
  /* 设置了 namespaced: true，这意味着它们的状态、mutations、actions 和 getters 都将被封装在自己的命名空间下。
  总之，namespaced: true 的作用是将 Vuex 模块的内容封装在命名空间下，以避免命名冲突并且更清晰地组织代码 */
  state () {
    return {
      // 个人权证 相关
      /*  userInfo: {
        token: '',
        userId: ''
      } */
      userInfo: getInfo()
      // getInfo()默认为空是与被注释的一样的内容 vuex的数据直接从本地拿,变相的实现了vuex的本地持久化

    }
  },

  mutations: {
    // 外部的组件 需要往user仓库里面存token了,就在mutations中写方法(用mutations则符合单项数据流)
    setUserInfo (state, obj) {
      state.userInfo = obj
      // 顺便往本地也存一份个人权证
      setInfo(obj)
    }
  },
  actions: {
    logout (context) {
      // 个人信息要重置
      context.commit('setUserInfo', {})
      // 购物车信息要重置(cart模块,所以要跨模块)
      context.commit('cart/setCartList', [], { root: true })
      //! 要跨模块,加 { root: true }
    }
  },
  getters: {

  }
}
