<template>
  <div v-if="item.goods_id" class="goods-item" @click="$router.push(`/prodetail/${item.goods_id}`)" >
    <div class="left">
      <img :src="item.goods_image" alt="" />
    </div>
    <div class="right">
      <p class="tit text-ellipsis-2">
        {{ item.goods_name }}
      </p>
      <p class="count">已售{{item.goods_sales }}件</p>
      <p class="price">
        <span class="new">¥{{item.goods_price_min}}</span>
        <span class="old">¥{{item.goods_price_max}}</span>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GoodsItem',
  data () {
    return {

    }
  },
  props: {
    item: {
      type: Object,
      // 父传子如果是个对象 要写默认值default,并且default还要是个函数并有返回值,返回一个新对象的方式来设置 props 的默认值
      // 使用函数返回一个新对象的方式可以确保每个组件实例都拥有自己的默认对象，而不会共享同一个引用。
      /*
      !不懂也没事,知道父传子是个对象时记得写default以及default值是个函数且要返回新对象就行
      如果多个子组件实例共享同一个父组件传递的对象类型的 props 的默认值，那么它们将共享同一个对象引用。
      这意味着当其中一个组件修改了这个对象的属性值时，其他组件也会受到影响，因为它们都引用的是同一个对象
      ————也就是说有19个商品,每一个商品为子组件,父组件循环了19次这个子组件,每个商品子组件里面又有详细商品的属性,
      其实19个商品子组件引用的其实是你在父组件中传递一个对象类型的 props 并且使用对象字面量的默认值，
      所有的子组件实例将共享相同的对象引用。这意味着每个子组件实例都会引用相同的默认值对象
      这意味着如果其中一个子组件修改了默认值对象的属性，其他子组件也会受到影响，因为它们共享相同的引用。
      */
      default: () => {
        return {}
      }
    }
  }
}
</script>

<style lang="less" scoped>
.goods-item {
  height: 148px;
  margin-bottom: 6px;
  padding: 10px;
  background-color: #fff;
  display: flex;
  .left {
    width: 127px;
    img {
      display: block;
      width: 100%;
    }
  }
  .right {
    flex: 1;
    font-size: 14px;
    line-height: 1.3;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    .count {
      color: #999;
      font-size: 12px;
    }
    .price {
      color: #999;
      font-size: 16px;
      .new {
        color: #f03c3c;
        margin-right: 10px;
      }
      .old {
        text-decoration: line-through;
        font-size: 12px;
      }
    }
  }
}
</style>
